.event-top {
  height: auto;
}
.evt-title{
    margin-top: 10%;
    margin-left: 5%;

    ul{
        display: flex;
        gap: 1.5%;
    }
}

.evt-title > ul > li:nth-child(2) {
  font-size: 4rem;
}

.event-mid{
  background-image: url(../../public/image/bg_pattern.png);
  background-color: #fffae847;
}


.mySwiper1-slide{
  width: auto;
  height: 500px;
}
.eventcook-pic{
  width: 100%;
  height: 100%;
  border-radius: 20px;
}

.eventcook-box > span{
  display: inline-block;
  padding-top: 4%;
  font-size: 1.7rem;
}

.event-info{
  position: absolute;
  z-index: 99;
  bottom: -72%;
  left: 50%;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background-color: #e6e6fa63;


  p{
    font-size: 1.1rem;
    position: relative;
    top: 35%;
    left: 4%;
  }
}
