/* Search 모듈용 CSS - searching.css  */

/* 검색입력창 박스 */
.searching{
    width: 250px;
    height: 40px;
    border-radius: 20px;
    color: #15a775;
    background-color: #efefef;
    box-sizing: border-box;
    padding: 0 5px;
}
.searching input{
    font-size: 1rem;
    line-height: 1.7;
    width: 77%;
    margin-top: 5px;
    margin-left: 10px;
    background-color: transparent;
    border: none;
}
input:focus{
    outline: none;
}
.schbtn{
    cursor: pointer;
    margin-top: 9px;
    margin-left: 10px;
}

.restit{
    font-size: min(39px,4vw);
    margin: 10px;
}

/* 검색박스 */
.schbx{
    
    display: flex;
    max-width: 1320px;
    min-height: 600px;
    padding: 20px;
    margin: 20px auto;
    /* border: 1px solid #000; */
}

/* 검색옵션박스 */
.schopt{
    width: 28%;
}
/* 캐릭터리스트 박스 */
.listbx{
    width: 70%;
}

.tit{
    height: 12vh;
}

/* 정렬박스 */
.sortbx{
    padding-bottom: 15px;
    overflow: hidden;
}
.sel{
    float: right;
    font-size: 1.1rem;
    font-weight: bold;
    border: none;
    letter-spacing: 1px;
}
.sel:focus{
    outline: none;
}

