/* searchingData CSS 리스트박스 */
.clist{
    display: flex;
    flex-wrap: wrap;
    gap: 3%;
}
.clist li{
    width: 31%;
    aspect-ratio: 3 / 3;
}
/* 캐릭터 이미지 */
.clist li img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 16px;
}
/* 캐릭터 이름 */
.clist li h3{
    height: 30%;
    padding: 5% 0%;
    text-align: center;
    // font-size: min(20px,1.7vw);
    font-size: 1.4vw;
    font-weight: normal;
    
   
}